
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
:root{
  --blue: #5521ff;
  --dark: #313031;
  --midDark: #404040;
  --lightDark: #808080;
  --lightGrey: #f5f5f5;
  --extraLightGrey: #f9f9f9;
}

/* some setup and utility stylings */
h1, h2, h3, h4, h5, h6{
  font-family: 'Epilogue', sans-serif;
  margin: 0.5rem 0;
}
img{
  width: 100%;
  display: block;
}
a{
  color: black;
  text-decoration: none;
}
ul li{
  list-style-type: none;
}
body{
  font-family: 'Inter', sans-serif;
  line-height: 1.6;
}
button{
  font-family: inherit;
  font-size: 1rem;
  outline: none;
}
.text-light{
  color: var(--dark);
  font-size: 0.95rem;
  font-weight: 300;
  margin: 0.5rem 0;
}
.container{
  width: 95vw;
  margin: 0 auto;
}
.py-5{
  padding: 5rem 0;
}

